*, *::before, *::after {
    box-sizing: border-box;
}

body {
    margin: 0;
    height: 100vh;
    width: 100vw;
    /* text-align: center; */
    background: linear-gradient(to right, teal, green);
}

.calc-cont {
    width: 300px;
    height: 500px;
    background-color: black;
    /* margin-top: 7rem; */
    /* position: relative;
    /* align-content: center; */
    margin-left: auto;
    margin-right: auto;
    margin-top: 150px;
    
}

.calc-grid {
    display: grid;
    height: 500px;
    /* width: 100%; */
    /* margin-top: 7rem; */
    grid-template-columns: 1fr, 1fr, 1fr, 1fr;
    grid-template-rows: 200px, 1fr, 0.5fr, 0.5fr, 0.5fr, 0.5fr;
    grid-template-areas: 
    'screen screen screen screen'
    'C neg per div'
    'sev eight nine mult'
    'four five six min'
    'one two three add'
    'zero dec equal equal';
    grid-gap: 5px;
    padding: 5px;
}

.output {
    grid-area: screen;
    border: 1px solid grey;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
    padding: 0.25rem;
    word-wrap: break-word;
    word-break: break-all;
}

.prev-operand {
    color: rgba(255, 255, 255, 0.75);
    font-size: 1.25rem;
}

.current-operand {
    color: violet;
    font-size: 1.75rem;
}

.calc-grid > button {
    cursor: pointer;
    font-size: 2rem;
    border: 1px solid black;
    outline: none;
}

.calc-grid > button:hover,
.calc-grid > button:focus {
    background-color:darkmagenta;
}

button {
    border-radius: 10px;
    background-color: blueviolet;
    border: none;
    font-weight: bold;
    font-size: 20px;
    color: whitesmoke;
    /* align-self: end; */
}

